.wrapper{
  width: 100%;
  position: relative;
  padding: 4px;
}

.line {
  position: absolute;
  width: 100%;
  height: 2px; /* Adjust thickness of the line */
  transform: translateY(-50%); /* Adjust for exact centering */
}

.marker_wrapper {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  .year {
    position: absolute;
    font-size: x-large;
    /* background-color: var(--BackgroundColor40);
    border-radius: 4px;
    box-shadow: 0px 0px 8px var(--BackgroundColor80); */
    margin: 0;
  }
}
.marker {
  height: 4px;
  width: 4px;
  background-color: var(--DarkRed);
  transform: rotate(45deg) translateY(-50%);
  border: 2px solid black;
}
